/* === RESET GÉNÉRAL === */
* {
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
}

a {
    text-decoration: none;
    color: inherit;
}

/* === TYPOGRAPHIE === */
@font-face {
    font-family: "Cormorant-Regular";
    src: url("../../assets/fonts/Cormorant_Garamond/static/CormorantGaramond-Regular.ttf");
}
@font-face {
    font-family: "Inter-Regular";
    src: url("../../assets/fonts/Inter/static/Inter_18pt-Regular.ttf"); // ⚠️ Corrigé la virgule mal échappée
}

body {
    font-family: "Inter-Regular", sans-serif;
    font-size: 1em; // 16px
    line-height: 1.6;
    color: #1c1c1c;
    background-color: #fff;
}

/* === TITRES === */
h1 {
    font-size: clamp(1.75em, 5vw, 3em); // ≈ 28–48px
    font-weight: 600;
    line-height: 1.25;
    text-align: center;
}

h2 {
    font-size: 1.75em;
    font-weight: 600;
    line-height: 1.3;
    font-family: "Cormorant-Regular", serif;
    color: #5a5a5a;
}

h3 {
    font-size: 1.25em;
    font-weight: 500;
    line-height: 1.3;
}

p {
    font-size: 1.125em;
    line-height: 1.75;
    font-weight: 400;
    margin-bottom: 1.5em;
}

/* === IMAGES === */
img {
    width: 100%;
    height: auto;
    object-fit: cover;
}

/* === CLASSES GÉNÉRIQUES === */
.active {
    color: #5a5a5a !important;
    font-weight: 600;
}

.nav-link {
    color: #1c1c1c;
}

/* === HÉRO (à déplacer dans home.scss ou component) === */
.hero {
    background: url("../../assets/images/background-hero.png") center/cover no-repeat;
}
